/* MEDIA======================================================================================= */
@media (min-width: 1100px) {
  .accomodation {
    width: 30%;
  }
}
@media (max-width: 1600px) {
  #details {
    height: max-content !important;
  }
  #rsvpInfo p {
    width: 80% !important;
  }
  #btnRsvp {
    width: 150px !important;
    height: 150px !important ;
    font-size: 1rem !important;
  }
}
@media (max-width: 1100px) {
  #mainImage {
    width: 75% !important;
    background-image: url(../images/centerPic.jpg);
    background-size: 100% !important;
  }
}
@media (max-width: 1000px) {
  h1 .bride {
    top: 30%;
  }
  .landing-page-content h2 {
    left: 50%;
    transform: translate(-50%);
    width: 100%;
  }
  .landing-page-content h4 {
    left: 50%;
    width: 100%;
    transform: translate(-50%);
  }

  /* WELCOME PAGE */
  .welcome .content {
    width: 100%;
  }
  .welcome p {
    width: 95%;
    font-size: 1rem;
  }
  .swan,
  .swan2 {
    width: 35%;
    top: 60%;
  }
  /* ENVELOPE */

  /* WEDDING DETAILS */
  #details .typing {
    color: #31496a;
    letter-spacing: 5px !important;
  }
  #details .typing.animate {
    animation: none !important; /* stop animation completely */
    width: auto !important; /* show full text */
    overflow: visible !important; /* no clipping */
    border-right: none !important; /* hide cursor */
  }

  /* DETAILS */

  #registry section {
    position: static !important;
    left: auto !important;
    right: auto !important;
    bottom: auto !important;
    transform: translate(0%, 0%) !important;
    width: 85% !important;
  }
}
@media (max-width: 900px) {
  nav {
    display: flex;
    justify-content: space-between;
    /* background-color: #f6f3ea; */
    opacity: 0.9;
    background-color: rgb(60, 83, 88);
    position: relative !important;
  }
  ul {
    width: 25%;
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    display: flex;
    justify-content: space-evenly;
    font-size: 1.4rem;
    align-items: center;
  }

  .logo {
    position: absolute !important;
    left: 50% !important;
    transform: translate(-50%) !important;
  }
  .links {
    cursor: none !important;
    visibility: hidden !important;
    display: none !important;
    opacity: 0 !important;
  }
}
/* =================================================================================== */
@media (max-width: 500px) {
  .landing-page {
    height: 70vh;
  }
}
@media (max-width: 750px) {
  .welcome h1 {
    letter-spacing: 20px;
  }
  #details .title {
    top: -80px !important;
  }
  .accomodation .hotel-info-wrapper {
    flex-direction: column; /* stack vertically */
    align-items: center;
    gap: 10px;
  }

  .hotel-name {
    transform: rotate(0deg); /* horizontal on mobile */
    font-size: clamp(1rem, 8vw, 5rem); /* adjust size */
    white-space: normal; /* allow wrapping if needed */
  }

  .hotel-info {
    align-items: flex-start; /* center info under name */
    gap: 15px;
  }

  /* ===================================RSVP============================================== */
  .rsvp {
    position: relative;
    width: 100%;
    height: 100vh;
    background-color: #586057;
    align-content: center; /*vertical*/
    aspect-ratio: 16 / 9; /* stays proportional on all screens */
  }
  .envelope {
    position: absolute;
    --envelope-size-width: 250px;
    --envelope-size-height: 250px;
    --envelope-size-hyp: 354px;
    /* width: var(--envelope-size-width) + var(--envelope-size-width / 2); */
    width: var(--envelope-size-width);
    height: var(--envelope-size-height);
    left: 50%;
    top: 70%;
    transform: translate(-50%, -50%);
  }
  .rsvp-content h1 {
    font-size: 1.2rem;
  }
  .rsvp-content p {
    font-size: 0.6rem;
  }
  #btnRsvp {
    width: 50px !important;
    height: 50px !important;
    font-size: 10px !important;
    border-radius: 50%;
    background-color: #586057;
  }
  #timer-section {
    width: 100% !important;
    height: 100vh !important;
    display: flex;
    flex-direction: column;
    margin-top: auto !important;
    justify-content: center !important;
    align-items: center !important;
  }
  #mainImage {
    width: 75% !important;
    background-image: url(../images/centerPic.jpg);
    background-size: 200% !important;
  }
  .countdown {
    width: 100% !important;
  }

  .timers {
    width: 100% !important;
  }

  #registry {
    position: relative;
    height: 80vh;
    align-content: center;
    overflow: hidden;
  }
  .left-img {
    position: absolute;
    right: 0% !important;
    top: 500px !important;
    z-index: 1 !important;
    width: 85% !important;
  }
  .right-img1,
  .right-img2 {
    position: absolute;
    right: -20px;
    top: 0;
    width: 35%;
    z-index: 1;
  }
  .right-img2 {
    right: -100px;
    top: 25px;
    transform: rotate(-20deg);
  }
  .gift-img {
    position: absolute;
    left: 31%;
    top: 25%;
    transform: scaleY(-1) rotate(-135deg);
    opacity: 0.2;
  }
  #registry section {
    position: static !important;
    display: flex;
    flex-direction: column;
    justify-content: top;
    align-items: center;
    left: auto !important;
    right: auto !important;
    bottom: auto !important;
    transform: translate(0%, 0%) !important;
    width: 85% !important;
  }
  #registry .content {
    width: 100% !important;
  }
  #registry button {
    width: 400px;
    height: 80px;
    background-image: url(../images/registry-btn.png);
    background-position: center;
    background-size: cover;
    border: none;
    background-color: transparent;
    color: white;
    font-size: 2rem;
  }
}
